.add-card .card-body {
  display: none;
}

.hidden-button {
  display: none;
}

.hidden-list {
  display: none;
}

.card-addheading {
  cursor: pointer;
  user-select: none;
}

.card-list {
  font-size: 1.1em;
}

.card-list .card-item {
  border-bottom: 1px dotted gray;
}

.card-list .card-item:last-child {
  border-bottom: none;
}

.card-list .card-name {
  font-weight: 600;
  color: #337ab7;
  font-size: 1.3em;
  line-height: 100%;
}

.card-list .label-item {
  font-weight: 600;
  color: #667b82;
}

.card-list .card-details {
  font-style: italic;
}

.card-list .card-desc {
  line-height: 120%;
}

.dropdown-item.active,
.dropdown-item:active {
  background-color: #a9aeb2;
}




nav {
  border-bottom: 4px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  z-index: 5;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-bottom: 8px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 8px 16px;
}

nav a:hover {
  background: black;
  color: white;
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 120px;
}